<template>
  <div class="image-uploader">
    <div class="uploader">
      <div 
        class="upload-area"
        @dragover.prevent
        @dragenter.prevent
        @drop.prevent="handleDrop"
        @click="triggerFileInput"
        :class="{ disabled: loading, 'drag-over': dragOver }"
        @dragenter="dragOver = true"
        @dragleave="dragOver = false"
      >
        <input 
          ref="fileInput"
          type="file"
          accept="image/*"
          @change="handleFileInput"
          style="display: none"
          :disabled="loading"
        >
        
        <div v-if="imageUrl" class="image-preview">
          <img :src="imageUrl" class="preview-image">
          <div class="re-upload-mask">ÈáçÊñ∞‰∏ä‰º†</div>
        </div>
        
        <div v-else class="upload-placeholder">
          <span class="upload-icon">üì§</span>
          <div class="upload-text">Â∞ÜÂõæÁâáÊãñÂà∞Ê≠§Â§ÑÔºåÊàñ<em>ÁÇπÂáª‰∏ä‰º†</em></div>
          <div class="upload-tip">ÊîØÊåÅ JPG/PNG/GIF/WEBP Ê†ºÂºèÔºåÂ§ßÂ∞è‰∏çË∂ÖËøá10MB</div>
        </div>
        
        <div class="upload-loading" v-if="loading">
          <div class="loading-spinner"></div>
          <p>ÂàÜÊûê‰∏≠...</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/**
 * @description ÂõæÁâá‰∏ä‰º†ÁªÑ‰ª∂
 */

export default {
  name: 'ImageUploader',
  data() {
    return {
      loading: false,
      imageUrl: null,
      dragOver: false
    };
  },
  methods: {
    triggerFileInput() {
      if (!this.loading) {
        this.$refs.fileInput.click();
      }
    },
    
    handleFileInput(event) {
      const file = event.target.files[0];
      if (file) {
        this.handleFile(file);
      }
    },
    
    handleDrop(event) {
      this.dragOver = false;
      const files = event.dataTransfer.files;
      if (files.length > 0) {
        this.handleFile(files[0]);
      }
    },
    
    async handleFile(file) {
      if (!this.validateFile(file)) {
        return;
      }
      
      this.loading = true;
      this.imageUrl = URL.createObjectURL(file);
      
      try {
        await this.analyzeImage(file);
      } catch (error) {
        this.handleError(error);
      }
    },
    
    validateFile(file) {
      const isImage = file.type.startsWith('image/');
      const isLt10M = file.size / 1024 / 1024 < 10;

      if (!isImage) {
        this.showMessage('Âè™ËÉΩ‰∏ä‰º†ÂõæÁâáÊñá‰ª∂ÔºÅ', 'error');
        return false;
      }
      if (!isLt10M) {
        this.showMessage('‰∏ä‰º†ÂõæÁâáÂ§ßÂ∞è‰∏çËÉΩË∂ÖËøá 10MBÔºÅ', 'error');
        return false;
      }
      
      return true;
    },
    
    showMessage(message, type = 'info') {
      // ÁÆÄÂçïÁöÑÊ∂àÊÅØÊèêÁ§∫ÂÆûÁé∞
      const messageEl = document.createElement('div');
      messageEl.className = `custom-message custom-message-${type}`;
      messageEl.textContent = message;
      
      Object.assign(messageEl.style, {
        position: 'fixed',
        top: '20px',
        left: '50%',
        transform: 'translateX(-50%)',
        padding: '12px 20px',
        borderRadius: '6px',
        color: 'white',
        fontSize: '14px',
        zIndex: '9999',
        transition: 'all 0.3s ease'
      });
      
      if (type === 'error') {
        messageEl.style.backgroundColor = '#F56C6C';
      } else if (type === 'success') {
        messageEl.style.backgroundColor = '#67C23A';
      } else {
        messageEl.style.backgroundColor = '#409EFF';
      }
      
      document.body.appendChild(messageEl);
      
      setTimeout(() => {
        messageEl.style.opacity = '0';
        setTimeout(() => {
          document.body.removeChild(messageEl);
        }, 300);
      }, 3000);
    },
    
    async analyzeImage(file) {
      console.log('ÂºÄÂßãÂàÜÊûêÂõæÁâá:', {
        fileName: file.name,
        fileSize: file.size,
        fileType: file.type
      });
      
      try {
        // Â∞ÜÂõæÁâáËΩ¨Êç¢‰∏∫base64
        const base64 = await this.fileToBase64(file);
        
        // Áõ¥Êé•Ë∞ÉÁî®Â§ßÊ®°ÂûãAPI
        const analysisResult = await this.callAIModel(base64);
        
        console.log('ÂàÜÊûêÊàêÂäü:', analysisResult);
        this.handleSuccess({ analysis: analysisResult });
        
      } catch (error) {
        console.error('ÂàÜÊûêÂ§±Ë¥•ËØ¶ÊÉÖ:', {
          message: error.message
        });
        
        throw error;
      }
    },
    
    async fileToBase64(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = () => resolve(reader.result);
        reader.onerror = reject;
        reader.readAsDataURL(file);
      });
    },
    
    async callAIModel(imageBase64) {
      const apiKey = '161ec8aa-2cb6-4c30-add2-e34ec5e0a94b';
      const baseUrl = 'https://ark.cn-beijing.volces.com/api/v3';
      const modelId = 'doubao-1-5-thinking-vision-pro-250428';
      
      const prompt = `ËØ∑‰∏•Ê†ºÊåâ‰ª•‰∏ãÁªìÊûÑÂåñÊ†ºÂºèÔºà‰ΩøÁî®‰∏≠ÊñáÔºâÂàÜÊûêÂõæÁâáÂπ∂ËæìÂá∫Ôºö

„ÄêÊúçË£ÖÂàÜÊûê„Äë
- Êï¥‰ΩìÊ¶ÇËø∞Ôºö2-3Âè•ÊèèËø∞È£éÊ†º/Ê∞õÂõ¥
- ÂçïÂìÅÊ∏ÖÂçïÔºöÈÄêÊù°ÂàóÂá∫"ÊòéÁ°ÆÂêçÁß∞ÔºöÁâàÂûãÔºåÊùêË¥®/Á∫πÁêÜÔºå‰∏ªËâ≤ÔºåÂõæÊ°àÔºåÂ≠£ËäÇ"ÔºåÈÅøÂÖçÊ≥õÂåñÂ¶Ç"‰∏äË°£/Ë£§Â≠ê"ÔºåÂ∫îÂÜô"ÁôΩËâ≤Áõ¥Á≠íÁâõ‰ªîË£§/Á±≥Ëâ≤ÁæäÊØõË•øË£ÖÂ§ñÂ•ó"Á≠â
- Âú∫ÊôØËØÜÂà´ÔºöÊòØÂê¶‰∏∫"ÈÖíÂêß/ÂíñÂï°ÂéÖ/Ë°óÂ§¥/Êµ∑Ëæπ/ÂÆ§ÂÜÖÂäûÂÖ¨/Ê†°Âõ≠"Á≠â
- Âú∫ÊôØÂç≥Êó∂Âª∫ËÆÆÔºöÂú®‰∏çÊîπÂä®‰∏ª‰ΩìÊúçË£ÖÁöÑÂâçÊèê‰∏ãÔºåËøΩÂä†2-3Êù°"ÈÖçÈ•∞/Â±ÇÊ¨°/ÈûãÂ±•/Â§ñÊê≠"ÁöÑ‰ºòÂåñÂª∫ËÆÆ„ÄÇ

„ÄêÁ©øÊê≠Âª∫ËÆÆ„Äë
- ‰∫∫Áâ©ÁâπÂæÅÔºöÊÄßÂà´ÁâπÂæÅ„ÄÅÂπ¥ÈæÑÊÑü„ÄÅ‰ΩìÂûãËΩÆÂªì„ÄÅÊ∞îË¥®„ÄÅËÇ§Ëâ≤ÂÄæÂêë
- ÂàÜÂú∫ÊôØÊé®ËçêÔºàÂè™Êé®Ëçê3‰∏™ÔºöÊó•Â∏∏ÂäûÂÖ¨/Á∫¶‰ºö/ÂïÜÂä°‰ºöËÆÆ/ÊúãÂèãËÅö‰ºö/È¢ÅÂ•ñÂÖ∏Á§º/ÈÄõË°óË¥≠Áâ©/Êµ∑Êª©Â∫¶ÂÅá/ÊóÖË°å/Â©öÁ§ºÁ≠â‰ªªÊÑè3‰∏™ÔºâÔºö
  1) ÊúçË£ÖÁªÑÂêàÔºà‰∏äË£Ö/‰∏ãË£Ö/Â§ñÂ•ó/ÈûãÂ±•ÔºâÔºåÊòéÁ°Æ"ÂêçÁß∞+È¢úËâ≤+ÂÖ≥ÈîÆÁâàÂûã"
  2) Áõ∏ÈÖçÈ¶ñÈ•∞„ÄÅÂ∏ΩÂ≠ê‰∏éÈÖç‰ª∂Ôºà2-3È°πÔºâ
  3) ÈÄÇÁî®ÁêÜÁî±‰∏éÈ£éÊ†ºÊïàÊûúÔºà1-2Âè•Ôºâ
- ÈÖçËâ≤‰∏éÂ±ÇÊ¨°ÔºöÊï¥‰ΩìÈÖçËâ≤ÊñπÊ°à‰∏éÂ±ÇÊ¨°ÁªìÊûÑ

[ÁªìÊûÑÂåñÊï∞ÁªÑËæìÂá∫]
- CLOTHES_IN_IMAGE=["ÁôΩËâ≤Áõ¥Á≠íÁâõ‰ªîË£§","Á±≥Ëâ≤ÁæäÊØõË•øË£ÖÂ§ñÂ•ó",...]
- OUTFIT_SCENES={
  "ÈÄöÂã§":["Á±≥Ëâ≤ÁæäÊØõË•øË£ÖÂ§ñÂ•ó","ÁôΩËâ≤Áúü‰∏ùË°¨Ë°´","ÁÅ∞Ëâ≤Ë•øË£ÖË£§","ÈªëËâ≤‰πêÁ¶èÈûã"],
  "Á∫¶‰ºö":[...],
  "Â∫¶ÂÅá":[...]
}
Ê≥®ÊÑèÔºö‰ª•‰∏ä‰∏§È°π‰∏∫Êú∫Âô®ÂèØËØªÔºåÊîæÂú®ÊñáÊú´ÔºåÈô§Ê≠§‰πãÂ§ñ‰∏çË¶ÅÂÜçËæìÂá∫ÂÖ∂‰ªñJSON„ÄÇ`;

      try {
        const response = await fetch(`${baseUrl}/chat/completions`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${apiKey}`
          },
          body: JSON.stringify({
            model: modelId,
            messages: [
              {
                role: 'user',
                content: [
                  {
                    type: 'image_url',
                    image_url: {
                      url: imageBase64
                    }
                  },
                  {
                    type: 'text',
                    text: prompt
                  }
                ]
              }
            ]
          })
        });
        
        if (!response.ok) {
          throw new Error(`APIË∞ÉÁî®Â§±Ë¥•: ${response.status}`);
        }
        
        const data = await response.json();
        const content = data.choices[0].message.content;
        
        // ÊèêÂèñÂπ∂ÊâìÂç∞Êï∞ÁªÑÂà∞ÊéßÂà∂Âè∞
        this.extractAndLogArrays(content);
        
        return {
          clothing_type: this.extractClothingType(content),
          style_tags: this.extractStyleTags(content),
          color_tags: this.extractColorTags(content),
          detailed_analysis: content,
          raw_response: content
        };
        
      } catch (error) {
        console.error('AIÊ®°ÂûãË∞ÉÁî®Â§±Ë¥•:', error);
        throw new Error(`AIÂàÜÊûêÂ§±Ë¥•: ${error.message}`);
      }
    },
    
    extractClothingType(content) {
      // ÁÆÄÂçïÊèêÂèñÊúçË£ÖÁ±ªÂûã
      if (content.includes('‰∏äË°£') || content.includes('TÊÅ§') || content.includes('Ë°¨Ë°´')) return '‰∏äË°£';
      if (content.includes('Ë£§Â≠ê') || content.includes('Áâõ‰ªîË£§')) return 'Ë£§Â≠ê';
      if (content.includes('Ë£ôÂ≠ê') || content.includes('ËøûË°£Ë£ô')) return 'Ë£ôË£Ö';
      return 'ÊúçË£Ö';
    },
    
    extractStyleTags(content) {
      const styles = [];
      const styleKeywords = ['‰ºëÈó≤', 'Ê≠£Âºè', 'ËøêÂä®', 'Êó∂Â∞ö', 'ÁÆÄÁ∫¶', 'Â§çÂè§', 'ÁîúÁæé', 'Â∏ÖÊ∞î', '‰ºòÈõÖ', 'ÂïÜÂä°'];
      styleKeywords.forEach(style => {
        if (content.includes(style)) styles.push(style);
      });
      return styles.slice(0, 5);
    },
    
    extractAndLogArrays(content) {
      try {
        // ÊèêÂèñCLOTHES_IN_IMAGEÊï∞ÁªÑ
        const clothesMatch = content.match(/CLOTHES_IN_IMAGE\s*=\s*\[(.*?)\]/);
        if (clothesMatch) {
          const clothesArray = clothesMatch[1].split(',').map(item => item.trim().replace(/['"]/g, ''));
          console.log('CLOTHES_IN_IMAGE:', clothesArray);
        }
        
        // ÊèêÂèñOUTFIT_SCENESÂØπË±°
        const outfitMatch = content.match(/OUTFIT_SCENES\s*=\s*\{([\s\S]*?)\}/);
        if (outfitMatch) {
          const outfitText = outfitMatch[1];
          const scenes = {};
          
          // ÂåπÈÖçÊØè‰∏™Âú∫ÊôØ
          const sceneMatches = outfitText.match(/"([^"]+)"\s*:\s*\[(.*?)\]/g);
          if (sceneMatches) {
            sceneMatches.forEach(match => {
              const sceneMatch = match.match(/"([^"]+)"\s*:\s*\[(.*?)\]/);
              if (sceneMatch) {
                const sceneName = sceneMatch[1];
                const items = sceneMatch[2].split(',').map(item => item.trim().replace(/['"]/g, ''));
                scenes[sceneName] = items;
              }
            });
          }
          
          console.log('OUTFIT_SCENES:', scenes);
        }
      } catch (error) {
        console.error('ÊèêÂèñÊï∞ÁªÑÊó∂Âá∫Èîô:', error);
      }
    },

    extractColorTags(content) {
      const colors = [];
      const colorKeywords = ['ÁôΩËâ≤', 'ÈªëËâ≤', 'ËìùËâ≤', 'Á∫¢Ëâ≤', 'ÁªøËâ≤', 'ÈªÑËâ≤', 'ÁÅ∞Ëâ≤', 'Ê£ïËâ≤', 'Á≤âËâ≤', 'Á¥´Ëâ≤'];
      colorKeywords.forEach(color => {
        if (content.includes(color)) colors.push(color);
      });
      return colors.slice(0, 5);
    },
    
    handleSuccess(response) {
      this.loading = false;
      if (response && response.analysis) {
        this.$emit('upload-success', response.analysis);
        this.showMessage('ÂõæÁâáÂàÜÊûêÂÆåÊàêÔºÅ', 'success');
      } else {
        this.handleError(new Error('ÂàÜÊûêÂìçÂ∫îÊ†ºÂºè‰∏çÊ≠£Á°Æ'));
      }
    },
    
    handleError(error) {
      this.loading = false;
      this.imageUrl = null;
      
      let message = '‰∏ä‰º†Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï';
      
      if (error.response) {
        const status = error.response.status;
        const data = error.response.data;
        
        switch (status) {
          case 400:
            message = data?.detail || data?.message || 'ËØ∑Ê±ÇÂèÇÊï∞ÈîôËØØ';
            break;
          case 401:
            message = 'Êú™ÊéàÊùÉÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï';
            break;
          case 413:
            message = 'Êñá‰ª∂ËøáÂ§ß';
            break;
          case 415:
            message = '‰∏çÊîØÊåÅÁöÑÊñá‰ª∂Ê†ºÂºè';
            break;
          case 500:
            message = 'ÊúçÂä°Âô®ÂÜÖÈÉ®ÈîôËØØ';
            break;
          default:
            message = `‰∏ä‰º†Â§±Ë¥• (${status})`;
        }
      } else if (error.code === 'ECONNABORTED') {
        message = '‰∏ä‰º†Ë∂ÖÊó∂ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•';
      }
      
      this.showMessage(message, 'error');
    }
  }
};
</script>

