<template>
  <div class="dashboard-page container">
    <div class="page-header">
      <h1>ÊàëÁöÑ‰∏ªÈ°µ</h1>
      <p>Âú®ËøôÈáåÂºÄÂßã‰Ω†ÁöÑÊó∂Â∞ö‰πãÊóÖÔºå‰∏ä‰º†ÂõæÁâáÔºåÂèëÁé∞Êñ∞ÁöÑÊó∂Â∞öÁÅµÊÑü„ÄÇ</p>
    </div>

    <div class="custom-row">
      <div class="custom-col upload-col">
        <div class="upload-section card">
          <h3>ÂºÄÂßãÂ±û‰∫é‰Ω†ÁöÑÂàÜÊûê</h3>
          <ImageUploader @upload-success="onUploadSuccess" />
        </div>
      </div>

      <div class="custom-col result-col">
        <div class="analysis-result-section card">
          <h3>AIÂàÜÊûêÁªìÊûú</h3>
          <AnalysisResult :analysis-data="currentAnalysis" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import ImageUploader from '@/components/ImageUploader.vue';
import AnalysisResult from '@/components/AnalysisResult.vue';

export default {
  name: 'Dashboard',
  components: {
    ImageUploader,
    AnalysisResult
  },
  data() {
    return {
      currentAnalysis: null
    };
  },
  methods: {
    onUploadSuccess(analysisData) {
      this.showMessage('ÂõæÁâáÂàÜÊûêÂÆåÊàêÔºÅ', 'success');
      this.currentAnalysis = analysisData;
    },
    
    showMessage(message, type = 'info') {
      // ÁÆÄÂçïÁöÑÊ∂àÊÅØÊèêÁ§∫ÂÆûÁé∞
      const messageEl = document.createElement('div');
      messageEl.className = `custom-message custom-message-${type}`;
      messageEl.textContent = message;
      
      Object.assign(messageEl.style, {
        position: 'fixed',
        top: '20px',
        left: '50%',
        transform: 'translateX(-50%)',
        padding: '12px 20px',
        borderRadius: '6px',
        color: 'white',
        fontSize: '14px',
        zIndex: '9999',
        transition: 'all 0.3s ease'
      });
      
      if (type === 'error') {
        messageEl.style.backgroundColor = '#F56C6C';
      } else if (type === 'success') {
        messageEl.style.backgroundColor = '#67C23A';
      } else {
        messageEl.style.backgroundColor = '#409EFF';
      }
      
      document.body.appendChild(messageEl);
      
      setTimeout(() => {
        messageEl.style.opacity = '0';
        setTimeout(() => {
          document.body.removeChild(messageEl);
        }, 300);
      }, 3000);
    }
  }
};
</script>
